@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&family=Raleway:wght@600&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navBar{
  width: 100%;
    font-family: var(--font-poppins);
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* position: fixed; */
  }
  
  .navBar div{
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .navBar .NomApp {
    font-weight: 600;
    font-size: 24px;
  }
  
  .navBar img {
    height: 64px;
    width: auto;
  }
  
  .Profile img {
    height: 42px;
    width: 42px;
    border-radius: 50%;
    margin: 0 10px 0 0;
  }
  
  .Profile h3{
    display: none;
  }

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    grid-gap: 50px;
    gap: 50px;;
  }
  
  li {
    font-weight: 500;
    font-size: 22px;
    cursor: pointer;
  }
  
  li.active {
    color: #3F8A9C;
  }

.buttons{
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}
  .buttons button {
    padding: 6px 10px 6px 10px;
    font-size: 22px;
    font-family: var(--font-poppins);
    font-weight: 600;
    color: #000000;
    background: transparent;
    border: solid #000000;
    border-radius: 10px;
  }
  
  #signin {
    color: #FFFFFF;
    background: #3F8A9C;
    border: solid #3F8A9C;
  }
* {
  margin: 0px 0px 0px 0px; 
  padding: 0px 0px 0px 0px; 
  border: 0;
  box-sizing: border-box;
}

:root {
  --font-poppins: "Poppins", sans-serif;
}

.Landing {
    background-color: #F5F5F0;
    padding: 20px 60px 20px 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 140px;
    gap: 140px;
  }
  
  h3{
    font-weight: 400;
    font-size: 24px;
    color: #3F8A9C;
  }

  h1{
    font-weight: 600;
    font-size: 64px;
  }

  p{
    font-weight: 400;
    font-size: 26px;
  }

  .home{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }

  .description{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 858px;
    grid-gap: 50px;
    gap: 50px;
  }

  .filler{
    height: 40px;
  }

  .links{
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 30px;
    gap: 30px;
}
  .links button {
    padding: 6px 10px 6px 10px;
    font-size: 22px;
    font-weight: 600;
    color: #000000;
    background: transparent;
  }

  #consulter {
    color: #FFFFFF;
    background: #3F8A9C;
    border: solid #3F8A9C;
    border-radius: 10px;
  }


  .showcase{
    display: block;
    position: relative;
  }

  .showcase .green{
    position: relative;
    top: 40px;
    left: 80px;
    background-color: #B4D381;
    border-radius: 26px;
    width: 616px;
    /* min-width: 264px; */
    height: 614px;
    z-index: 1;
    /* max-height: 614px;
    min-height: 262px; */
  }

  /* .showcase .first{
    position: relative;
    top: -520px;
    left: 0px;
    z-index: 2;
    box-shadow: -5px 10px 4px 0px rgba(0, 0, 0, 0.1);
    border-radius: 16px;
  } */

  .showcase .first{
    position: absolute;
    top: 100px;
    left: 0px;
    z-index: 2;
    box-shadow: -5px 10px 4px 0px rgba(0, 0, 0, 0.1);
    border-radius: 16px;
  }

  .showcase .second{
    position: absolute;
    top: -50px;
    left: 280px;
    z-index: 3;
    box-shadow: -5px 10px 4px 0px rgba(0, 0, 0, 0.1);
    border-radius: 16px;
  }

  .news, .tendance{
    width: 100%;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .news img{
    position: relative;
    z-index: 1;
    min-height: 530px;
    width: auto;
    border-radius: 26px;
  }

  .Carousel .new{
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: row;
  }

  .eventInfos{
    padding-left: 40px;
    padding-right: 40px;
    background-color: #3F8A9C;
    font-size: 20px;
    font-weight: 400;
    color: #FFFFFF;
    width: 534px;
    height: 384px;
    border-radius: 26px;
    display: flex;
    flex-direction: column;
    grid-gap: 40px;
    gap: 40px;
    justify-content:center;
    position: relative;
    left: -20px;
    z-index: 2;
  }

  .line {
    height: 1px;
    background-color: white;
  }

  .tendance .imageContainer {
    height: 360px;
    width: 300px;
    background-color: #B4D381;
    border-radius: 26px;
  }

  .tendance img{
    width: 80%;
    height: 100%;
    max-height: auto;
    border-radius: 26px;
    object-fit: contain;
  }

  .tendance .titre{
    font-weight: 500;
    font-size: 26px;
  }

  .tendance button.rec-dot{
    display: none;
  }
  
  .eventInfos .titre{
    font-size: 28px;
    font-weight: 500;
  }

  .rec.rec-arrow:hover, .rec.rec-arrow:active, .rec.rec-arrow:focus{
    background-color: rgba(180, 211, 129, 1) !important;
  }
  .rec.rec-arrow:disabled {
    visibility: hidden;
  }
  
  .rec-carousel-item:focus {
    outline: none;
    box-shadow: inset 0 0 1px 1px #B4D381;
  }
  
  button.rec-dot:hover, button.rec-dot:active, button.rec-dot:focus, button.rec-dot_active{
    background-color: rgba(180, 211, 129, 0.5) !important;
    box-shadow: 0 0 1px 3px rgb(180, 211, 129, 1)
  }
  
  button.rec-arrow{
    background-color: rgba(180, 211, 129, 0.3);
    color: #000000;
  }

  .about{
    text-align: center;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    grid-gap: 40px;
    gap: 40px;
  }

  @media screen and (max-width: 880px){
  
  }

  @media screen and (max-width: 768px){
    p {
      font-size: 12px;
    }
    h1{
      font-size: 24px;
    }
  }

  @media screen and (max-width: 540px){
    
    .about{
      padding-left: 10px;
      padding-right: 10px;
    }
  }
.footer{
    width: 100%;
    max-width: 900px;
    text-align: center;
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    background-color: #F3EEDC;
    border-radius: 26px;
    flex-wrap: wrap;
}

.footer .logos{
    display: flex;
    flex-direction: row;
    grid-gap: 40px;
    gap: 40px;
}

.footer img{
    max-width: 200px;
    height: auto;
    object-fit: contain;
}

.footer .madeWith{
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}

.footer .inline{
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}

.footer h3{
    font-weight: 500;
    font-size: 24px;
    color: #000000;
}

.footer .madeWith img{
    max-height: 64px;
    width: auto;
}

.footer .madeWith p{
    font-weight: 400;
    font-size: 20px;
    color: #000000;
}

.footer .madeWith h4{
    font-weight: 400;
    font-size: 20px;
    color: #3F8A9C;
    text-decoration: underline;
}

.footer .contacts{
    width: 100%;
    max-width: 380px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.footer .mini{
    width: 100%;
    max-width: 380px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

@media screen and (max-width: 880px) {
    .footer .made{
        flex-direction: column;
    }

}

@media screen and (max-width: 768px) {
    .footer p, .footer h4 {
        font-size: 12px;
    }
    .footer h1{
        font-size: 24px;
    }
    .footer h3{
        font-size: 18px;
    }
    .footer img{
        max-width: 140px;
    }
}

@media screen and (max-width: 540px) {
    .footer .inline{
        flex-direction: column;
    }
    .footer img{
        max-width: 80px;
    }
    .footer .contacts{
        max-width: 120px;
    }
    .footer .mini{
        flex-direction: column;
        grid-gap: 20px;
        gap: 20px;
    }
}
.leaflet-container {
    width: 100%;
    height: 100vh;
  }
  
  .marker-cluster{
    background-color: #3488e169;
    margin-left: -20px;
    margin-top: -20px;
    width: 50px !important;
    height: 50px !important;
    -webkit-transform: translate3d(657px, 177px, 0px);
            transform: translate3d(657px, 177px, 0px);
    border-radius: 50%;
    z-index: 177;
    opacity: 1;
  }
  .marker-cluster div {
    background-color: #3487e1 !important;
    color: white !important;
    width: 40px;
    height: 40px;
    margin-left: 5px;
    margin-top: 5px;
    text-align: center;
    box-sizing: border-box;
    padding-top: 4px;
    border-radius: 50%;
    font: 20px "Helvetica Neue", Arial, Helvetica, sans-serif;
  }
  
  
  .map-container {
    height: 100vh;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
  }
  
  .map {
    height: 100%;
    width: 100%;
    z-index: 1;
  }
  
  
  
  .input-inset{
    z-index: 10;
    font-size: 16px;
    line-height: 1.5;
    background: #FFFFFF;
    background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='19' height='19' viewBox='0 0 19 19'><path fill='%23838D99' d='M13.98 12.57a2 2 0 0 1 1.93.52l2.5 2.5a2 2 0 0 1-2.82 2.82l-2.5-2.5a2 2 0 0 1-.52-1.93l-1.38-1.37a7 7 0 1 1 1.42-1.42l1.37 1.38zm-3.37-2.03a5 5 0 1 0-7.08-7.08 5 5 0 0 0 7.08 7.08z'></path></svg>");
    background-repeat: no-repeat;
    background-position: 10px 10px;
    background-size: 20px 20px;
    border: 1px solid #C5CBD5;
    box-shadow: inset 0 1px 4px 0 rgba(0,0,0,0.20);
    border-radius: 8px;
    padding: .5em 1em .5em 2.5em;
    width: 300px;
    margin-bottom: -50px;
    margin-top: 8px;
    margin-left: 50px;
    font-size: 16px;
    } 
    
    .input-inset::-webkit-input-placeholder{
      color: #838D99;
    } 
    
    .input-inset::placeholder{
      color: #838D99;
    }
    
    .input-inset:focus {
      outline: none;
      border: 1px solid #84A2FA;
    }
  
    .main{
      display: flex;
    }
  
    .main .state{
      width: 800px;
      height: 100vh;
      overflow-y: scroll;
    }
  
    .main .state img {
      width: 100%;
      max-height: 390px;
    }
    .main .state .informations{
      padding: 0px 40px;
      margin-top: -4px;
      background-color: #F5F5F0;
      min-height: calc(100vh - 247px);
    }
  
    .informations h1{
      font-size: 35px;
      margin-left: 10px;
    }
  
    .informations h4{
      font-weight: 300;
      font-size: 20px;
      text-decoration: underline;
      text-transform: lowercase;
    }
  
    .informations h3{
      font-weight: 400;
      font-size: 22px;
    }
    .informations h5{
      font-weight: 300;
      font-size: 20px;
      margin: 0;
    }
  
    .informations .icons{
      display: flex;
    }
    .informations .icons image{
      width: 40px;
    }
    .informations .title{
      display: flex;
      align-items: center;
    }
  
    .informations .rating{
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .informations .rating span{
      color: #538403;
    }
  
    .coments{
      margin: 0 15px;
    }
  
  
    .coments h3{
      margin: 0;
    }
  
    .coments input {
      margin-top: 15px;
      width: 100%;
      height: 64px;
      border-radius: 10px;
      border: 1px solid #DEDEDE;
      padding-left: 20px;
      font-size: 18px;
      outline: none;
    }
  
    .coments button{
      display: none;
    }
.App {
  background-color: #F5F5F0;
  text-align: center;
}

.login {
    width: 100vw;
    height: 100vh;
    background-color: #3F8A9C;
    position: relative;
    display: flex;
    justify-content: flex-end;
}

.login form{
    width: 50%;
    min-width: 800px;
    background-color: white;
    border-radius: 50px 0 0 50px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.login .logo{
    position: absolute;
    top: 10px;
    left: 40px;
    height: 64px;
    width: auto;
}

.login input{
    width: 500px;
    height: 40px;
    border: none;
    border-bottom: 1px solid #A6A6A6;
    font-size: 26px;
    margin: 15px 0;
    padding-left: 5px;
}

.login input:focus{
    outline: none;
}

.login form .inputs{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login form button{
    width: 500px;
    height: 74px;
    border: none;
    background-color: #3F8A9C;
    color: white;
    font-size: 26px;
    margin-top: 30px;
    border-radius: 25px;
}

.login form span{
    color: #3F8A9C;
    text-decoration: none;
}
